.admin-file_upload {
  display: flex;
  background: rgb(244, 245, 249);
  min-height: 100vh;
  .admin-home__content {
    width: calc(100% - 70px);
    @include r(767) {
      width: calc(100% - 45px); } }
  .breadcrumbs {
    @include r(767) {
      margin-bottom: 30px; } }
  .swiper-admin-file {
    .btn-update {
      position: absolute;
      top: 2px;
      right: 100px;
      padding: 8px 18px;
      font-size: 12px; } }
  &__slider {
    .swiper-slide-thumb-active {
      .admin-file_upload__slider__block {
        box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
        border-color: $mango;
        &:before {
          opacity: 1; }
        &__text {
          background-color: $very-light-pink-six; } } }
    &__block {
      border-radius: 10px;
      box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
      background-color: $white;
      display: flex;
      border: solid 1px transparent;
      @include tr;
      position: relative;
      &:before {
        width: 20px;
        height: 20px;
        content: '';
        position: absolute;
        bottom: -10px;
        left: 50%;
        transform: translate(-50%) rotate(45deg);
        background: $very-light-pink-six;
        border: 1px solid $mango;
        z-index: 3;
        opacity: 0;
        @include tr; }
      &__text {
        flex: 1;
        width: 155px;
        position: relative;
        z-index: 4;
        padding: 21px;
        border-radius: 10px;
        @include r(767) {
          padding: 11px; }
        h2 {
          font-size: 18px;
          font-weight: bold;
          color: $charcoal-grey;
          display: flex;
          align-items: flex-start;
          position: relative;
          padding-right: 30px;
          padding-bottom: 8px;
          img {
            width: 18px;
            padding-top: 1px;
            margin-left: 10px; }
          span {
            position: absolute;
            right: 0;
            font-size: 12px;
            font-weight: 600;
            color: rgba(19, 28, 46, 0.6); } }
        strong {
          font-size: 12px;
          font-weight: 600;
          color: $dark;
          padding-bottom: 8px;
          display: block; }
        p {
          font-size: 12px;
          color: rgba(19, 28, 46, 0.6);
          span {
            display: block; } } }
      &__btn {
        display: flex;
        padding-top: 15px;
        a {
          @include r(767) {
            font-size: 11px; } }
        .btn-primary {
          margin-right: 10px; } } }
    .gallery-top {
      padding: 50px 5px 5px;
      &__slide {
        &__title {
          display: flex;
          @include r(767) {
            flex-direction: column; }
          h2 {
            font-size: 25px;
            font-weight: normal;
            color: $dark;
            display: flex;
            margin-bottom: 20px;
            span {
              font-size: 12px;
              font-weight: 600;
              color: rgba(19, 28, 46, 0.6);
              margin-left: 6px; } }
          h3 {
            font-size: 14px;
            font-weight: bold;
            color: $charcoal-grey;
            margin-left: 30px;
            margin-right: 20px;
            position: relative;
            top: 10px;
            @include r(767) {
              margin: 0 0 15px; } }
          .selectize-input {
            top: -2px;
            background: none;
            @include r(767) {
              margin-bottom: 15px; } } }
        &__warning {
          border-radius: 10px;
          border: solid 0.5px $orangey-red;
          background-color: $light-pink;
          padding: 12px 15px;
          p {
            font-size: 14px;
            font-weight: 600;
            color: $strawberry;
            @include center;
            justify-content: flex-start;
            i {
              font-size: 20px;
              margin-right: 10px; } } }
        &__table {
          width: 100%;
          overflow: auto;
          border-radius: 10px;
          box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
          background-color: $white;
          margin-top: 20px;
          &::-webkit-scrollbar-track {
            background-color: $very-light-pink; }
          &::-webkit-scrollbar-thumb {
            border-radius: 2px;
            background: #fda729; }
          &::-webkit-scrollbar {
            width: 7px;
            height: 7px; }
          table {
            min-width: 1110px;
            width: 100%;
            display: block; }
          thead, tbody, tfoot, tr {
            width: 100%;
            display: block; }
          tr {
            display: flex;
            border-top: 1px solid $very-light-pink-five; }
          th, td {
            width: 25%;
            display: block;
            text-align: left;
            border-right: 1px solid $very-light-pink-five;
            padding: 20px 20px;
            font-size: 14px;
            font-weight: normal;
            color: $dark-grey;
            @include r(970) {
              padding: 10px 15px; }
            &:last-child {
              border-right: none; }
            &.error-red {
              background-color: $light-peach;
              color: $strawberry;
              font-weight: 600;
              @include center-y;
              i {
                font-size: 20px;
                margin-right: 10px; } }
            &.error-yellow {
              background-color: $pale;
              color: $mango;
              font-weight: 600;
              @include center-y;
              i {
                font-size: 20px;
                margin-right: 10px; } } }
          thead {
            tr {
              border-top: none; }
            th {
              background: rgb(246, 247, 247); } }
          tfoot {
            td {
              background: rgb(246, 247, 247); }
            strong {
              font-size: 12px;
              font-weight: bold;
              color: $dark;
              margin-bottom: 13px;
              display: block;
              text-transform: uppercase; }
            h4 {
              font-size: 14px;
              font-weight: normal;
              color: $dark;
              margin-bottom: 13px;
              b {
                font-weight: bold; } }
            span {
              font-size: 14px;
              font-weight: normal;
              color: $mango;
              margin-bottom: 13px;
              display: block;
              b {
                font-weight: bold; } }
            p {
              font-size: 14px;
              font-weight: normal;
              color: $orangey-red;
              b {
                font-weight: bold; } } } } } } }
  &__btn {
    margin-top: 40px;
    display: flex;
    justify-content: flex-end;
    @include r(767) {
      margin-top: 20px; }
    a {
      margin-left: 9px;
      font-size: 14px;
      font-weight: bold;
      width: 125px;
      @include center;
      @include r(767) {
        padding: 8px 15px; } }
    a.btn-primary {
      border: solid 1px $mango;
      color: $mango;
      width: 100px;
      &:hover {
        background: $mango;
        color: $white; } }
    a.btn-cansel {
      border: solid 1px rgba(19, 28, 46, 0.4);
      color: rgba(19, 28, 46, 0.4);
      &:hover {
        background: rgba(19, 28, 46, 0.4);
        color: $white; } } }
  &__log {
    position: fixed;
    right: -100%;
    top: 0;
    max-width: 715px;
    width: 100%;
    height: 100vh;
    z-index: 12;
    background-color: $very-light-pink-three;
    box-shadow: -1.5px 2px 16px 0 rgba(2, 23, 56, 0.19);
    transition: all 0.8s;
    &.is-active {
      right: 0; }
    &--bg {
      position: fixed;
      left: 0;
      top: 0;
      display: none;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.7);
      z-index: 11; }
    &__title {
      padding: 30px 40px;
      padding-right: 15px;
      background: $white;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid rgba($charcoal-grey, 0.15);
      @include r(767) {
        padding: 10px 15px;
        flex-direction: column; }
      &__text {
        display: flex;
        align-items: center;
        @include r(767) {
          flex-direction: column; } }
      a {
        font-size: 25px;
        font-weight: 600;
        color: $charcoal-grey;
        &:hover {
          color: $mango; }
        i {
          color: $mango;
          font-size: 20px;
          margin-right: 34px;
          position: relative;
          bottom: -2px; } }
      p {
        font-size: 18px;
        font-weight: normal;
        color: $charcoal-grey;
        margin-left: 20px;
        @include r(767) {
          margin-left: 0;
          margin-top: 15px; } } }
    &__table {
      height: calc(100vh - 96px);
      overflow: auto;
      padding-bottom: 30px;
      table {
        min-width: 740px;
        width: 100%;
        display: block; }
      thead, tbody, tfoot, tr {
        width: 100%;
        display: block; }
      tr {
        display: flex;
        align-items: center;
        border-bottom: 1px solid rgba($charcoal-grey, 0.15);
        @include tr;
        &.is-active {
          background: $ice-blue; } }
      td {
        display: block;
        padding: 25px 0;
        @include r(767) {
          padding: 15px 0; }
        input {
          display: none;
          &:checked + label:before {
            border-color: $bright-light-blue;
            background: $bright-light-blue;
            color: $white; } }
        label {
          cursor: pointer;
          &:before {
            content: $icon-checked;
            font-family: "iconfont";
            border: solid 1px #767676;
            border-radius: 3px;
            width: 16px;
            height: 16px;
            font-size: 10px;
            color: transparent;
            @include tr;
            @include center; } }
        strong {
          font-size: 14px;
          font-weight: bold;
          color: $charcoal-grey; }
        b {
          font-size: 12px;
          font-weight: 600;
          color: $dark;
          padding-bottom: 8px;
          display: block; }
        p {
          font-size: 12px;
          color: rgba(19, 28, 46, 0.6); }
        span {
          font-size: 14px;
          font-weight: normal;
          color: $mango;
          padding-left: 30px; }
        a {
          font-size: 12px;
          font-weight: bold;
          color: $bright-light-blue;
          &:hover {
            text-decoration: underline; } }
        &:first-child {
          width: 75px;
          display: flex;
          justify-content: flex-end;
          padding-right: 17px; }
        &:nth-child(2) {
          width: 30px;
          img {
            width: 21px;
            padding-top: 3px; } }
        &:nth-child(3) {
          width: 80px; }
        &:nth-child(4) {
          width: 40px;
          img {
            width: 16px; } }
        &:nth-child(5) {
          max-width: 225px; }
        &:nth-child(6) {
          width: 170px; }
        &:last-child {
          width: 100px; } } } } }

